import React, { useState } from 'react';
import { Container, Grid, makeStyles, CardHeader } from '@material-ui/core';
import Page from 'src/components/Page';
import LatestOrders from './LatestOrders';
import PerformanceSummary from './PerformanceSummary';
import TodaysChange from './TodaysChange';
import TotalFollowers from './TotalFollowers';
import TotalBalance from './TotalBalance';
import OverviewDonut from './OverviewDonut';
import FollowBar from './FollowBar';
import axios from 'axios';

const useStyles = makeStyles(theme => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingBottom: theme.spacing(3),
    paddingTop: theme.spacing(3)
  }
}));

const Dashboard = () => {

  const [todaysChange, setTodaysChange] = useState()
  const [totalFollowers, setFollowers] = useState()
  const [portfolioAllocations, setPortAllocations] = useState()
  const [totalBalance, setBalance] = useState()

  const classes = useStyles();

  const loadTodaysChange = () => {
    axios.get("/performance/overall-performance?days=1")
    .then(res => {
        setTodaysChange(res) // returns at Number that represents a percent.
    })
    .catch(err => console.log(err));
  }
  
  const loadFollowers = () => {
    axios.get("/user/followers")
    .then(res => {
        console.log("followers", res)
        setFollowers(res) // returns at Number that represents a percent.
    })
    .catch(err => console.log(err));
  }

  const loadPortfolioAllocations = () => {
    axios.get("/portfolios/portfolio-allocations")
    .then(res => {
        setPortAllocations(res) // returns at Number that represents a percent.
        // example response : {name: "My portfolio", startingValue: "1300"}
    })
    .catch(err => console.log(err));
  }

  const loadSummedPortfolios = () => { // Total Amount - all of portfolios current values summed up.
    axios.get("/performance/total-balance")
    .then(res => {
        setBalance(res) // returns at Number that represents a percent.
    })
    .catch(err => console.log(err));
  }

   //loadFollowers()
   loadTodaysChange()
   //loadPortfolioAllocations()
   //loadSummedPortfolios()

  return (
    <Page className={classes.root} title="Dashboard">
      <Container maxWidth={false}>
        <Grid container spacing={3}>
          <Grid item lg={3} sm={6} xl={3} xs={12}>
            <TodaysChange change={todaysChange ? `${todaysChange}%` : 'Loading'} />
          </Grid>
          <Grid item lg={3} sm={6} xl={3} xs={12}>
            <TotalFollowers count={totalFollowers ? `${totalFollowers}%` : 'Loading'} />
          </Grid>
          <Grid item lg={3} sm={6} xl={3} xs={12}>
            <TotalBalance balance={totalBalance}/>
          </Grid>
          <Grid item lg={8} md={12} xl={9} xs={12}>
            <PerformanceSummary />
          </Grid>
          <Grid item lg={4} md={6} xl={3} xs={12}>
            <OverviewDonut portfolios={portfolioAllocations} />
          </Grid>
          <Grid item lg={4} md={6} xl={3} xs={12}>
            <CardHeader title="Followers Gained/Lost" />
            <FollowBar />
          </Grid>
          <Grid item lg={8} md={12} xl={9} xs={12}>
            <LatestOrders />
          </Grid>
        </Grid>
      </Container>
    </Page>
  );

};




export default Dashboard;

